
.header{
    padding: 9px 20px;
    display: flex;
    justify-content: space-between;
    position: sticky;
    background-color: white;
    box-shadow: 0px 5px 8px -9px black;
}
.header__input{
    display: flex;
    align-items: center;
    background-color:#f5f2f2;
    padding:10px;
    margin-left:10px;
    border-radius: 999px;
}
.header__input > input {
    border: none;
    background-color: transparent;
    outline-width: 0;
}
.header__left{
    display: flex;
    /* justify-content: space-evenly; */
}
.header__left > img {
    height: 49px;
}
.header__middle{
    color: gray;
    display: flex;
    /* flex: 1; */
    /* justify-content: center; */
}

.header__option > .MuiSvgIcon-root {
    color: gray;
}
.header__option:hover > .MuiSvgIcon-root {
    color: blue;
}
.header__option {
    display: flex;
    align-items: center;
    padding: 0 30px;
    cursor: pointer;
    
}
.header__option:hover {
    background-color: #f5f2f2;
    border-radius: 3px;
    align-items: center;
    padding: 0 30px;
    
}
#header__option--active > .MuiSvgIcon-root {
    color: blue;
    
}
#header__option--active {
    border-bottom: 4px solid blue;
    
}
.header__info {
    display: flex;
    align-items: center;
    
}
.header__right{
    display:flex;
}
.header__info > h4 {
    margin-left: 10px;
}

